body {
    background-color: #21262d;
}

.book-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: auto;
    max-width: 1100px;
    min-width: 300px;
}

/** BOOK STUFF **/
.book {
    width: 300px;
    height: 450px;
    display: flex;
    flex-direction: column;
    margin: 20px;
    border-radius: 20px;
    transition: all .2s ease-in-out;
    cursor: pointer;

}

    /** title **/
    .book h1 {
        font-family: 'Bebas Neue', serif;
        font-size: 3rem;
        padding-left: 10px;
        padding-right: 10px;
        line-height: 90%;
        letter-spacing: 2px;
        text-align: center;
        margin-top: 20%;
    }

    .book h2 {
        text-align: center;
        margin-bottom: -3rem;
    }

    /** author **/
    .book h3 {
        font-family: 'Lato', sans-serif;
        font-size: 1rem;
        text-align: center;
        font-style: italic;
        color: #212121;
        margin-top: -2rem;
    }

    .book h4 {
        font-family: 'Lato', sans-serif;
        font-size: 1rem;
        text-align: center;
        margin-top: -1rem;

    }

    .book p {
        text-align: center;
        padding: 15px;
        margin-top: -1.5rem;
        font-size: 0.75rem;
        font-family: 'Lato', sans-serif;
    }

    .book q {
        text-align: center;
        font-family: 'Lato', sans-serif;
        padding: 15px;
        margin-top: -2rem;
        font-style: italic;
        font-size: 0.75rem;
    }
